import React, { Component } from "react";
import {
  Row,
  Col,
  Card,
  CardBody,
  CardTitle,
  Button,
  Form,
  Label,
  ButtonGroup
} from "reactstrap";
import Validator from "./validator";
import InputBanky from "../../../../components/input-banky/input-banky";
import { toCurrency } from "../../../../util/currency-formatter";

export class ScheduleBoleto extends Component {
  state = {
    statusScheduleBoleto: false,
    mensal: {
      value: "",
      date: ""
    }
  };

  constructor(props) {
    super(props);
    this.validators = Validator;
  }

  handleComponentChanges = input => e => {
    const { mensal: formFields } = this.state;

    formFields[input] =
      input === "value" ? toCurrency(e.target.value) : e.target.value;
    this.setState({ mensal: formFields });
  };

  handleFormClick = () => {
    const { statusScheduleBoleto } = this.state;
    if (this.validForm())
      this.setState({ statusScheduleBoleto: !statusScheduleBoleto });
  };

  validForm = () => {
    let status = true;
    const { mensal: formFields } = this.state;
    Object.keys(formFields).forEach(field => {
      const { [field]: currentState } = formFields;
      this.formValidators(field, currentState);
      if (!this.validators[field].valid) {
        status = false;
      }
    });
    return status;
  };

  formValidators(fieldName, value) {
    this.validators[fieldName].errors = [];
    this.validators[fieldName].state = value;
    this.validators[fieldName].valid = true;
    this.validators[fieldName].rules.forEach(rule => {
      if (rule.test instanceof RegExp) {
        if (!rule.test.test(value)) {
          this.validators[fieldName].errors.push(rule.message);
          this.validators[fieldName].valid = false;
        }
      } else if (typeof rule.test === "function") {
        if (!rule.test(value)) {
          this.validators[fieldName].errors.push(rule.message);
          this.validators[fieldName].valid = false;
        }
      }
    });
    this.setState(st => st);
  }

  showErrors(fieldName) {
    if (fieldName === "") {
      return <div className="error" />;
    }
    const validator = this.validators[fieldName];

    if (validator && !validator.valid) {
      const errors = validator.errors.map((info, index) => (
        // eslint-disable-next-line react/no-array-index-key
        <span className="error" key={index}>
          <i className="mdi mdi-alert-octagram" /> {info}
        </span>
      ));
      return <div className="error">{errors}</div>;
    }
    return <div className="error" />;
  }

  render() {
    const {
      mensal: { value, date },
      statusScheduleBoleto
    } = this.state;
    return (
      <Card className="scheduleRechargeComponent">
        <CardBody>
          <Row className="justify-content-around">
            {/* Título */}
            <Col xs="12">
              <CardTitle className="section-card-title">
                <i className="ti-back-right" /> Recarga Automática
              </CardTitle>
            </Col>
            <Col xs="12" className="my-3">
              <b>O que é?</b>
              <p className="text-justify">
                Deixe programado para receber todo mês um boleto para a recarga
                do seu cartão Banky. Assim você não se preocupa em toda vez ter
                que gerar um boleto para carga do cartão.
              </p>
            </Col>
            <Col xs="12">
              <Form className="form-material">
                <Row>
                  <Col xs="12">
                    <Row>
                      <Col xs="6">
                        <Label>
                          Qual valor que você deseja carregar em seu cartão todo
                          mês?
                        </Label>
                      </Col>
                      <Col xs="6">
                        <Label>
                          Qual dia do mês que deseja receber o boleto?
                        </Label>
                      </Col>
                      <Col xs="6">
                        <InputBanky
                          label="Valor"
                          type="text"
                          onChange={this.handleComponentChanges("value")}
                          value={value}
                          name="value"
                          mask={this.validators.value.mask}
                          formNoValidate
                          required
                        />
                        {this.showErrors("value")}
                      </Col>
                      <Col xs="6">
                        <InputBanky
                          label="Dia do mês"
                          type="text"
                          onChange={this.handleComponentChanges("date")}
                          value={date}
                          name="date"
                          mask={this.validators.date.mask}
                          formNoValidate
                          required
                        />
                        {this.showErrors("date")}
                      </Col>
                    </Row>
                  </Col>
                </Row>
              </Form>
            </Col>
            <Col xs="12" className="text-center my-3">
              <b>Status da função</b>
            </Col>
            <Col xs="12" className="text-center">
              {/* <Button
                type="button"
                onClick={this.handleFormClick}
                className="btn btn-info"
              >
                Salvar envio
              </Button> */}
              <ButtonGroup className="toggler">
                <Button
                  color="primary"
                  onClick={this.handleFormClick}
                  active={statusScheduleBoleto === true}
                >
                  Ativo
                </Button>
                <Button
                  color="primary"
                  onClick={this.handleFormClick}
                  active={statusScheduleBoleto === false}
                >
                  Inativo
                </Button>
              </ButtonGroup>
            </Col>
          </Row>
        </CardBody>
      </Card>
    );
  }
}

export default ScheduleBoleto;



